import React, { useEffect, useState } from 'react';
import Link from "next/link";
import { fetchBooks } from '@/API/booksApi';

interface Book {
  id: number;
  type: string;
  // Add other properties as needed
}

export default function Books() {
  const [books, setBooks] = useState<Book[]>([]); // State to store books
  const [loading, setLoading] = useState<boolean>(true); // State for loading
  const [error, setError] = useState<string | null>(null); // State for error handling

  useEffect(() => {
    const fetchAllBooks = async () => {
      try {
        const booksData = await fetchBooks();
        setBooks(booksData); // Update the state with the books data
      } catch (error) {
        setError("Failed to fetch books");
        console.error(error);
      } finally {
        setLoading(false); // Set loading to false once data is fetched
      }
    };

    fetchAllBooks(); // Call the fetch function when the component mounts
  }, []); // Empty dependency array ensures this runs once when the component mounts

  if (loading) {
    return <p>Loading...</p>; // Display loading message while fetching data
  }

  if (error) {
    return <p>{error}</p>; // Display error message if there's an error
  }

  return (
    <main className="min-h-screen w-full flex items-center justify-center bg-background flex-col gap-5">
      <h1 className="text-2xl">Welcome to the Books Page üìö</h1>
      
      {/* Check if books is an array before mapping */}
      {books.length > 0 ? (
        books.map((book) => (
          <div key={book.id}>
            <p>{book.id}</p> {/* Render each book's id */}
            <p>{book.type}</p> {/* Render each book's type */}
          </div>
        ))
      ) : (
        <p>No books available</p> // Fallback message if no books
      )}

      <Link href="/">
        <button className="bg-white text-black px-4 py-2 rounded-md hover:bg-slate-200 duration-300 ease-in-out">
          Go Back üè†
        </button>
      </Link>
    </main>
  );
}
